/**
 * @fileOverview reset基础样式
 */
* {
    webkit-tap-highlight-color: transparent;
}
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    /*vertical-align: baseline; delete by jican*/
}
html, body {
    width: 100%;
    height: 100%;
}
a {
    color: inherit;
    text-decoration: none;
}
a:focus,a:hover,a:visited {
    outline: 0;
    text-decoration: none;
}
a:active,a:hover {
    outline: 0;
}
div {
    -webkit-tap-highlight-color: rgba(0 , 0, 0, 0);
}

input{
    outline:none;
    font-size: 14px;
}
html, body, form, fieldset, p, div, h1, h2, h3, h4, h5, h6 {
    -webkit-text-size-adjust: none;
}
h1, h2, h3, h4, h5, h6 {
    font-size: 14px;
    font-weight: normal;
}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
}
a{
    text-decoration: none;
}
body {
    -webkit-text-size-adjust:none;
    font-size: 14px;
    color: #333;
    background: #f5f5f5;
    font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,PingFang SC,Source Han Sans CN,Hiragino Sans GB,Microsoft YaHei,WenQuanYi Micro Hei,sans-serif;
}

ol, ul {
    list-style: none;
}

blockquote, q {
    quotes: none;
}

img,table{
    border:0
}

blockquote:before, blockquote:after, q:before, q:after {
    content: '';
    content: none;
}

ins {
    text-decoration: none;
}

del {
    text-decoration: line-through;
}

select {
    -webkit-appearance:button;
    border: 0px;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

.clearfix:after {
    content: ' ';
    display: block;
    height: 0;
    clear: both;
}

.clearfix {
    *zoom: 1;
}
@charset "UTF-8";
html, body{
    position: relative;
    width: 100%;
    height: 100%;
}

#root{
    position: relative;
    width: 100%;
    height: 100%;
}

.App{
    position: absolute;
    margin: auto;
    width: 75%;
    height: 80%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #fff;
    box-shadow: 0 0 8px #ccc;
}

.styleguide,.styleguide *
{
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
}

.clearfix:after,.clearfix:before
{
    content: " ";
    display: table;
}
.clearfix:after
{
    clear: both;
}
.rt {
    position: relative;
}
.ab {
    position: absolute;
}
.tar {
    text-align: right;
}
.ft12 {
    font-size: 12px;
}
.bold {
    font-weight: bold;
}
.-vcenter:before
{
    content: "";
    display: inline-block;
    height: 100%;
    margin-right: -4px;
    vertical-align: middle;
}
.text-overflow {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.-hcenter
{
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}
.none {
    display: none;
}
.mt10 {
    margin-top: 10px;
}
.mt20 {
    margin-top: 20px;
}
.row
{
    display: -webkit-flex!important;
    -webkit-align-items: center;
    -webkit-box-align: center;
    width: 100%;
}
.-align-top.btn-group,.-align-top.flex-row,.row.-align-top
{
    -webkit-align-items: flex-start;
    -webkit-box-align: start;
}
.row>*
{
    display: block!important;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
}
.row>.-col-auto
